:root {
  --color-almost-black: rgb(50, 50, 50);
  --color-almost-white: rgb(246, 246, 246);
  --color-pink: rgb(235, 222, 215);
  --color-yellow: rgb(237, 236, 192);
  --color-green:	rgb(142, 189, 180);
  --color-purple: rgb(196, 200, 244);
  --color-grey: rgb(220, 220, 220);
  --color-blue: rgb(190, 224, 248);
  --color-manatee: rgb(159, 158, 186);
  --color-tumbleweed: rgb(230, 167, 138);
  --color-sage: rgb(197, 209, 159);
  --color-cadet-grey: rgb(150, 167, 179);
  --color-wild-blue-yonder: rgb(149, 166, 204);
  --color-peter-blue: rgb(126, 157, 171); }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.calendar-body {
  background: -webkit-gradient(linear, left top, right top, from(var(--color-almost-white)), color-stop(25%, var(--color-almost-white)), color-stop(25%, transparent), to(transparent));
  background: -webkit-linear-gradient(left, var(--color-almost-white), var(--color-almost-white) 25%, transparent 25%, transparent 100%);
  background: linear-gradient(to right, var(--color-almost-white), var(--color-almost-white) 25%, transparent 25%, transparent 100%); }

.calendar-body .calendar-grid-item {
  width: calc(10vw - 2px);
  display: inline-block; }
  .calendar-body .calendar-grid-item > .calendar-grid-item-number {
    text-align: right;
    margin-right: 5px; }
  .calendar-body .calendar-grid-item > .entry {
    height: 1em; }

.calendar-body .calendar-block {
  height: 10vh;
  vertical-align: top; }

.calendar-body .calendar-block.today {
  color: red; }

.calendar-wrapper {
  width: 70vw;
  margin: 0 15vw;
  position: relative;
  text-align: center; }
  .calendar-wrapper > .calendar-header > .current-month {
    font-size: xxx-large;
    margin-bottom: 50px; }
  .calendar-wrapper > .calendar-header > .calendar-navigation {
    position: absolute;
    width: 70vw; }
    .calendar-wrapper > .calendar-header > .calendar-navigation > button {
      border: none;
      background-color: transparent;
      font-size: xxx-large;
      cursor: pointer; }
    .calendar-wrapper > .calendar-header > .calendar-navigation > .button-previous {
      float: left; }
    .calendar-wrapper > .calendar-header > .calendar-navigation > .button-next {
      float: right; }
  .calendar-wrapper > .calendar-header > .day-legend {
    position: relative;
    background-color: var(--color-almost-white); }
    .calendar-wrapper > .calendar-header > .day-legend > .calendar-legend {
      width: calc(10vw - 2px);
      display: inline-block;
      font-size: x-large; }
    .calendar-wrapper > .calendar-header > .day-legend > .days-underline {
      height: 4px;
      width: 100vw;
      position: absolute;
      bottom: -4px;
      left: -15vw; }

.triangle-bottomright {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 0;
  height: 0;
  border-left: 62vw solid transparent;
  border-bottom: 75vh solid black; }

@media (max-width: 599px) {
  .calendar-body {
    background: -webkit-gradient(linear, left top, right bottom, from(var(--color-almost-white)), color-stop(70%, var(--color-almost-white)), color-stop(70%, transparent), to(transparent));
    background: -webkit-linear-gradient(top left, var(--color-almost-white), var(--color-almost-white) 70%, transparent 70%, transparent 100%);
    background: linear-gradient(to bottom right, var(--color-almost-white), var(--color-almost-white) 70%, transparent 70%, transparent 100%); }
  .calendar-body .calendar-grid-item {
    width: calc(12vw - 2px); }
  .calendar-wrapper {
    width: 84vw;
    margin: 0 8vw; }
    .calendar-wrapper > .calendar-header {
      margin-top: 30px; }
      .calendar-wrapper > .calendar-header > .current-month {
        font-size: x-large;
        margin-bottom: 30px; }
      .calendar-wrapper > .calendar-header > .calendar-navigation {
        width: 84vw; }
        .calendar-wrapper > .calendar-header > .calendar-navigation > button {
          font-size: x-large; }
      .calendar-wrapper > .calendar-header > .day-legend > .calendar-legend {
        font-size: medium;
        width: calc(12vw - 2px); }
  .calendar-body .calendar-block .location {
    display: none; }
  .triangle-bottomright {
    border-left-width: 80vw;
    border-bottom-width: 50vh;
    z-index: -1; } }

.circle {
  height: 250px;
  width: 250px;
  border-radius: 50%;
  display: inline-block;
  position: absolute; }

@media (max-width: 599px) {
  .circle {
    display: none; } }

.month-recap {
  border: solid 2px var(--color-almost-white);
  padding: 1em;
  bottom: 2em;
  right: 15vw;
  position: absolute; }
  .month-recap > .month-recap-group {
    display: inline-block;
    vertical-align: top;
    width: 25vw; }
    .month-recap > .month-recap-group > .month-recap-entry {
      position: relative; }
      .month-recap > .month-recap-group > .month-recap-entry > .background {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: none;
        z-index: -1; }
      .month-recap > .month-recap-group > .month-recap-entry > .left {
        display: inline-block;
        vertical-align: top;
        width: 47.5%;
        margin-right: 5%;
        text-align: right; }
      .month-recap > .month-recap-group > .month-recap-entry > .right {
        display: inline-block;
        vertical-align: top;
        width: 47.5%;
        text-align: left; }

@media (max-width: 599px) {
  .month-recap {
    width: calc(84vw - 2em);
    padding: 1em;
    bottom: 1em;
    right: 8vw; }
    .month-recap > .month-recap-group {
      width: 100%; }
      .month-recap > .month-recap-group > .month-recap-entry > .background {
        display: block; } }

.screen {
  height: 100vh;
  width: 100vw;
  position: relative; }

.home {
  color: var(--color-almost-black); }
  .home > .triangle-topleft {
    width: 0;
    height: 0;
    border-top: 75vh solid black;
    border-right: 62vw solid transparent;
    position: absolute;
    z-index: -1; }
  .home > .home-wrapper {
    width: 70vw;
    position: absolute;
    text-align: center;
    margin: 0 15vw;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }
    .home > .home-wrapper a {
      text-decoration: none;
      color: var(--color-almost-black); }
    .home > .home-wrapper > .left-mock {
      width: 50%;
