    .home > .home-wrapper > .left {
      display: inline-block;
      vertical-align: top;
      width: 50%;
      position: absolute;
      left: 0;
      top: 0;
      height: 100%; }
      .home > .home-wrapper > .left > .photo {
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        height: 100%; }
      .home > .home-wrapper > .left > .credit {
        width: 100%;
        text-align: right;
        font-family: Timeless;
        font-size: small; }
    .home > .home-wrapper > .right {
      display: inline-block;
      vertical-align: top;
      width: 50%;
      position: relative; }
      .home > .home-wrapper > .right > .right-wrapper {
        background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(50%, transparent), color-stop(50%, var(--color-almost-white)), to(var(--color-almost-white)));
        background: -webkit-linear-gradient(top, transparent, transparent 50%, var(--color-almost-white) 50%, var(--color-almost-white) 100%);
        background: linear-gradient(to bottom, transparent, transparent 50%, var(--color-almost-white) 50%, var(--color-almost-white) 100%);
        overflow: auto; }
        .home > .home-wrapper > .right > .right-wrapper > .right-with-margin {
          width: 80%;
          margin-left: 10%; }
          .home > .home-wrapper > .right > .right-wrapper > .right-with-margin > .title {
            font-family: BsSignature;
            font-size: 150px; }
          .home > .home-wrapper > .right > .right-wrapper > .right-with-margin > .text {
