            .home > .home-wrapper > .right > .right-wrapper > .right-with-margin > .text > p {
              margin-bottom: 30px; }
          .home > .home-wrapper > .right > .right-wrapper > .right-with-margin > .signature {
            font-family: BsSignature;
            font-size: 50px;
            text-align: right;
            margin-bottom: 20px; }

@media (max-width: 599px) {
  .home {
    height: 200vh; }
    .home > .home-wrapper {
      width: 84vw;
      margin: 0 8vw; }
      .home > .home-wrapper > .left-mock {
